import React from 'react';
import ReactPlayer from 'react-player';
import 'aframe';
import AFRAME from 'aframe';

// Import the Artyom library
import Artyom from 'artyom.js';

// Import the previously created class to handle the commands from another file
import ArtyomCommandsManager from './ArtyomCommands';

const Jarvis = new Artyom();

export default class Conversation extends React.Component {
  constructor (props, context){
      super(props, context);


      // Prepare simple state
      this.state = {
          artyomActive: false,
          textareaValue: "",
          artyomIsReading: false,
          textState: "Presiona sobre el botón iniciar nuestra conversación", 
          begin: true,
          isplayingVideo: false,
          isbuttonPlay: false,
          isvideoLoaded:false,
      };

      this.playButton = this.playButton.bind(this);
      // Load some commands to Artyom using the commands manager
      
  }


  firstplay;

  componentDidMount(){
    /*document.getElementById('videoPanel').addEventListener('onLoadEnd', function() { 
        console.log("video cargado");
        document.getElementById('videoPanel').play();
    });*/
    

    localStorage.setItem("playing", false);
    //this.firstplay = localStorage.getItem('playing');
    this.setState({isvideoLoaded:localStorage.getItem('playing')})
  }
 


  playvideo(){
    //if()
    var promise = document.getElementById('videoPanel').play();
    if (promise !== undefined) {
        promise.then(_ => {
            this.setState({isvideoLoaded:true})
        }).catch(error => {
            console.log("presiona sobre el boton para iniciar");
            this.setState({isvideoLoaded:false})
        });
    }
  }

  playButton(){
    document.getElementById('videoPanel').play();
    this.setState({isvideoLoaded:true})
  }

  render() {
    let {videoPath} = this.props;

    let buttonPlay = "";
    if(this.state.isvideoLoaded){
        buttonPlay = ""
    }else{
        buttonPlay = <div style={{width:"100%", zIndex: 1000}} >
        <button className="btn btn-primary " style={{zIndex: 1}} onClick={this.playButton}> Comenzar </button>
    </div> 
    }
    return (

        <div style={{height:"90%", width:"100%"}}>
            <a-scene embedded vr-mode-ui="enabled: false"   cursor="rayOrigin: mouse" raycaster="objects: #playButton">
                <a-entity 
                    light="color: #ffffff; type: ambient; intensity: 2" 
                    data-aframe-default-light="" 
                    aframe-injected="" 
                    position="">
                </a-entity>

                
                <a-assets>
                    {/*<img id="Micro" src={require("../images/micro.png")}/>*/}
                    <a-asset-item id="cityModel" src={require("./models/office_computer/scene.glb") }></a-asset-item>
                    <video 
                        //autoPlay 
                        preload="auto" 
                        autoload = "true"
                        id="videoPanel" 
                        src={videoPath}
                        onLoadedData={this.playvideo.bind(this)}>
                    </video>

                    <img id="playButton" geometry="primitive: plane" src={require("../images/playIcon.png")}/>
                </a-assets>
                
                <a-entity 
                    geometry="primitive: plane; width: 4; height: 4" 
                    position="0 .772 -3" 
                    rotation="-90 0 0" 
                    material="color: #7BC8A4" />

                <a-entity video-controls="src:#videoPanel"></a-entity>

                <a-gltf-model 
                    src="#cityModel" 
                    position="-0.444 0.890 -1.817"
                    scale="0.01 0.01 0.01" 
                    rotation="0 88.649 0" >
                </a-gltf-model>

                <a-video src="#videoPanel" 
                    width="3.2" 
                    height="1.8" 
                    position="-0.421 2.257 -3.1"
                    rotation="0 -2.343 0">
                </a-video>
                
               
                {/*<a-image 
                    cursor-listener
                    src="#playButton" 
                    position="-0.421 2.257 -3"
                    rotation="0 -2.343 0"
                    width="1.5"
                    height="1.5"
                >
                        
                </a-image>*/}
            </a-scene> 
            {buttonPlay}          
        </div>
    )
  }
}